    <div
      class="item"
      v-for="(item,index) in list.slice((pageNum - 1)*pageSize,pageNum*pageSize)"
      :key="index"
    >
      <div class="img_container">
        <img @click="$router.push({path:item.url})" class="img" :src="item.img" alt />
      </div>

      <div class="content">
        <h3 @click="$router.push({path:item.url})" class="overflow_1">{{item.title}}</h3>
        <h5 class="font-weight-normal" v-if="item.tips">{{item.tips}}</h5>
        <p :class="item.tips? 'overflow_4':'speClass'">
          {{item.content1}}
          <br v-if="item.content2" />
          {{item.content2}}
          <br v-if="item.content3" />
          {{item.content3}}
          <br v-if="item.content4" />
          {{item.content4}}
          <span @click="$router.push({path:item.url})">阅读详情></span>
        </p>
      </div>
    </div>
    <div
      v-if="list.slice((pageNum - 1)*pageSize,pageNum*pageSize).length != 6"
      style="font-size:13px;color:#cccccc;text-align: center;margin: 40px auto 100px;"
    >已到最后一条，暂无更多动态！</div>
    <Pagination
      :pageSize="pageSize"
      :pageTotal="pageTotal"
      :pageNum="pageNum"
      @pageCallBlack="pageCallBlack"
    />
  </div>
