<script>
import newsList from "./news_list";
import api from "@/api";
import Pagination from "@/components/common/pagination.vue";
export default {
  name: "News_Company",
  components: {
    Pagination
  },
  data() {
    return {
      list: [],
      pageSize: 6,
      pageTotal: null,
      pageNum: 1
    };
  },
  created() {
    // this.getNewsList();
  },
  mounted() {
    this.getData();
  },
  methods: {
    getNewsList() {
      this.list = newsList.company;
      this.pageTotal = newsList.company.length;
    },
    pageCallBlack(index) {
      this.pageNum = index;
      console.log(this.pageNum);
    },
    //跳转到路由详情
    goToDetail(id) {
      this.$router.push({
        name: "News_Company_Detail",
        params: {
          id: id
        }
      });
    },

    getData() {
      //获取公司动态资讯列表
      const that = this;
      this.$axios
        .post("/v1/information/queryInfoDynamicsPublishedPage", {
          moduleType: "公司动态",
          pageNum: 1,
          pageSize: 100
        })
        .then(function(res) {
          console.log(res.data.list);
          that.pageTotal = res.data.total;
          that.list = res.data.list;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.news_list_box {
  margin: 0 auto;

  .item {
    padding: 36px 0px;
    border-bottom: 1px solid #dadce0;
    box-sizing: border-box;
    transition: all ease 0.5s;
    -moz-transition: all ease 0.5s;
    -webkit-transition: all ease 0.5s;
    .img {
      width: 348px;
      height: 230px;
      float: left;
    }
    .img:hover {
      cursor: pointer;
    }

    .content {
      width: 718px;
      float: left;
      margin-left: 65px;
      h3 {
        line-height: 21px;
