        }
      }
    }
  }
  .item::after {
    content: "";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }
  .item:hover {
    box-shadow: 0px 6px 13px #dedede;
    padding-left: 30px;
    transition: all ease 0.5s;
    -moz-transition: all ease 0.5s;
    -webkit-transition: all ease 0.5s;
  }
}
